.App {
  background-color: rgb(209, 209, 209);

  min-width: 100%;
  min-height: 100%;
  position: absolute;

  font-size: 22px;
}

.header{
  display: inline-flex;
  justify-content: space-between;
  align-items: center;

  background-color: white;

  position: fixed;
  top: 0;  
  left: 0;
  z-index: 100;
  width: 100%;
  height: 10rem;

  box-shadow: 0 -10px 15px 0px black;

  font-size: 1rem;
}

.about-me{
  display: inline-flex;
  justify-content: flex-start;
  min-width: 50%;

  font-size: 1rem;
}

.profile-img{
  display: flex;
  align-items: center;
  align-self: center;

  padding: 0.1rem 0.1rem;
  margin-left: 1rem;
  border-radius: 50%;
  border: 3px solid black;
}

.profile-img img{
  height: 5em;
  width: 5em;
  border-radius: 50%;
  border: 1px solid white;
}

.intro{
  display: flex;
  flex-direction: column;
  align-self: center;
  
  text-align: left;
  font-size: 1rem;

  margin-left: 1rem;
}

.intro .description{
  font-size: 0.9rem;
  color: rgb(73, 71, 71);
}
.intro .name{
  font-size: 1.2rem;
  font-weight: bold;
  color: black;
}

.contact{
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;

  font-size: 1rem;
}

#linkedin {
  height: 2.8rem;
}
#mail {
  height: 2.5rem;
}

.content {
  display: flex;
  flex-direction: column;
  flex-grow: 10;
  min-width: 100%;
  /* min-height: fit-content; */
  margin-top: 10rem;
  height: 500px;

  background-color: rgb(209, 209, 209);

  position: relative;
  font-size: 1rem;

  overflow: scroll;
}

.heading{
  background-color: white;
  align-self: center;
  border-radius: 15px;
  padding: 0.7rem 2.5rem;
  box-shadow: 0 0 10px 0px rgb(156, 156, 156);
  margin-top: 1rem;

  font-size: 1rem;
}

.heading h1{
  margin: 0 0;
  font-size: 1.5rem;
}

.project-list{
  /* padding-top: 25%; */
  margin-top: 1.5rem;
  position: relative;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;

  font-size: 1rem;
}

.project-container{
  background-color: rgb(85, 83, 83);

  width: 14rem;
  height: 9rem;

  margin: 1rem;
  padding: 1rem;

  border-radius: 20px;

  font-size: 1rem;
}

.project-container:hover{
  box-shadow: 0 0 20px 0px white;
}

.project-container h4{
  font-size: 1.2rem;
  color: white;
}
.description{
  font-size: 1rem;
  color: white;
}

a{
  color: black;
  text-decoration: none;
}